<style>
</style>

<script>
import { useQuasar } from "quasar";
import { ref } from "vue";
import Header from "../../components/Header.vue";

const setup = {

  url: process.env.VUE_APP_URL_API_COLLABORATORS

};

export default {
  name: "Home",
  search: "Pesquisar",
  components: {
    Header,
  },
  setup() {

    const $q = useQuasar();
    const submitting = ref(false);
    const expanded = ref(false);

    return {
      setup: setup,
      expanded,
      submitting,
      data: null,
      user: null,
      collaborator: null,
      pageTitle: "Colaborador",
      pageSubTitle: "Pessoa autorizada",
      showNotif(msg, color, icon) {
        $q.notify({
          message: msg,
          color: color,
          icon: icon,
        });
      },
    };
  },

  data() {
    return {
      name: null,
      email: null,
      password: null,
      role: null,
      type: null,
      crmv: null,
      phone: null,
      photo: null,
      status: null,
      phonelength: null
    };
  },
  methods: {
    async getCollaborator() {
      const req = await fetch(this.setup.url + "/" + this.$route.params.id);
      this.data = await req.json();
      this.user = this.data.data.user;
      this.collaborator = this.data.data.collaborator;
      
      this.id = this.collaborator.id,
      this.name = this.collaborator.name,
      this.email = this.collaborator.email,
      this.crmv = this.collaborator.crmv,
      this.phone = this.collaborator.phone,
      this.photo = this.collaborator.avatar,
      this.username = this.user.username;
      this.status = this.user.status;
      this.type = this.user.type;

      this.phonelength = this.phone.length

      switch (this.collaborator.role) {
        case 'vet': this.role = 'Veterinário(a)'
        break
        case 'assistant': this.role =  'Assistente'
        break
        case 'technical': this.role =  'Técnico'
        break
        case 'expert': this.role =  'Especialista'
        break
        default: this.role =  ''
      }

      switch (this.user.type) {
        case 'vet': this.type = 'Veterinário(a)'
        break
        case 'assistant': this.type =  'Assistente'
        break
        case 'invited': this.type =  'Convidado'
        break
        case 'expert': this.type =  'Especialista'
        break
        case 'admin': this.type =  'Administrator'
        break
        default: this.type =  ''
      }

      switch (this.user.status) {
        case 'deleted': this.status = 'Deletado'
        break
        case 'active': this.status =  'Ativo'
        break
        case 'inactive': this.status =  'Inativo'
        break
        case 'blocked': this.status =  'Bloqueado'
        break
        default: this.status =  ''
      }

    },

  },

  mounted() {
    this.getCollaborator();
  },
};
</script>
